import React, { useEffect, useRef, useState } from 'react';
import styles from './GamePage.module.css';
import videoApi from '../../shared/api/videoApi';

const PlayerVideo = ({ 
  stream, 
  playerId, 
  playerName, 
  character, 
  isCurrentPlayer, 
  isVideoEnabled = true, 
  isAudioEnabled = true, 
  muted 
}) => {
  const videoRef = useRef(null);
  const [hasVideo, setHasVideo] = useState(false);
  const [videoError, setVideoError] = useState(null);
  const [videoStatus, setVideoStatus] = useState('connecting'); // 'connecting', 'active', 'error'

  useEffect(() => {
    console.log('PlayerVideo:   ', playerId, stream ? `: ${stream.getVideoTracks().length}, : ${stream.getAudioTracks().length}` : ' ');
    
    if (!stream || !videoRef.current) {
      console.log('   ', playerId, '   ');
      return;
    }

    const videoElement = videoRef.current;
    const videoTrack = stream.getVideoTracks()[0];
    
    if (videoTrack) {
      console.log(' ', playerId, ':', {
        enabled: videoTrack.enabled,
        active: videoTrack.active,
        type: videoTrack.kind,
        readyState: videoTrack.readyState,
        muted: videoTrack.muted
      });
    }

    //    
    if (videoElement.srcObject) {
      console.log('    ', playerId);
      videoElement.srcObject = null;
    }

    //   
    videoElement.srcObject = stream;
    
    //   
    const handlePlay = () => {
      console.log('   ', playerId);
    };

    const handleError = (error) => {
      console.error('  :', error);
      //   
      setTimeout(() => {
        if (videoElement.paused) {
          console.log('   ', playerId);
          videoElement.play().catch(err => {
            console.error('   :', err);
          });
        }
      }, 1000);
    };

    const handleLoadedMetadata = () => {
      console.log('   ', playerId);
      //   
      videoElement.play().catch(err => {
        console.error('   :', err);
      });
    };

    videoElement.addEventListener('play', handlePlay);
    videoElement.addEventListener('error', handleError);
    videoElement.addEventListener('loadedmetadata', handleLoadedMetadata);

    //   
    return () => {
      console.log('  PlayerVideo ', playerId);
      videoElement.removeEventListener('play', handlePlay);
      videoElement.removeEventListener('error', handleError);
      videoElement.removeEventListener('loadedmetadata', handleLoadedMetadata);
      
      if (videoElement.srcObject) {
        videoElement.srcObject.getTracks().forEach(track => track.stop());
        videoElement.srcObject = null;
      }
    };
  }, [stream, playerId]);

  //    
  const handleVideoError = (error) => {
    console.error(`    ${playerId}:`, error);
    setVideoError(error.message || '  ');
    setHasVideo(false);
    setVideoStatus('error');
  };

  //    
  const handleVideoPlay = () => {
    console.log(`    ${playerId}`);
    setVideoStatus('active');
    setHasVideo(true);
  };

  // ,   
  const showVideo = stream && 
    (hasVideo || stream.getVideoTracks().length > 0 || stream.isCanvasStream);

  return (
    <div className={styles.playerVideoContainer}>
      {stream ? (
        <div className={styles.videoWrapper}>
          <video
            ref={videoRef}
            className={styles.playerVideo}
            autoPlay
            playsInline
            muted={muted}
            onError={handleVideoError}
            onPlay={handleVideoPlay}
          />
          <div className={`${styles.videoStatus} ${styles[`videoStatus${videoStatus.charAt(0).toUpperCase() + videoStatus.slice(1)}`]}`}>
            <span className={styles.streamIcon}>
              {videoStatus === 'connecting' && ''}
              {videoStatus === 'active' && ''}
              {videoStatus === 'error' && ''}
            </span>
          </div>
        </div>
      ) : (
        <div className={styles.placeholderVideo}>
          <div className={styles.placeholderContent}>
            <span role="img" aria-label="user"></span>
            <div className={styles.noVideoText}>
              {videoError ? ' ' : '...'}
            </div>
            <div className={styles.spinnerContainer}>
              <div className={styles.spinner}></div>
            </div>
          </div>
        </div>
      )}
      {!isVideoEnabled && stream && (
        <div className={styles.videoDisabled}>
          <span className={styles.noVideoIcon}></span>
        </div>
      )}
      <div className={styles.playerInfo}>
        <div className={styles.playerName}>
          {playerName || ` ${playerId?.substring(0, 4) || ''}`} {isCurrentPlayer && ' ()'}
        </div>
        {character && (
          <div className={styles.playerCharacter}>
            : {isCurrentPlayer ? '???' : character}
          </div>
        )}
        {!isAudioEnabled && (
          <div className={styles.mediaControls}>
            <span className={styles.mutedIcon}></span>
          </div>
        )}
      </div>
    </div>
  );
};

export default PlayerVideo; 