import socketApi from './socketApi';

class MediasoupApi {
  constructor() {
    this.initialized = false;
    this.roomId = null;
    this.onTrackCallbacks = new Map();
  }

  //   
  async init(roomId) {
    console.log('MediasoupApi:  ');
    this.roomId = roomId;
    this.initialized = true;
    return true;
  }

  //     
  async getLocalStream() {
    console.log('MediasoupApi:    ');
    return null;
  }

  //    
  async publishStream() {
    console.log('MediasoupApi:   ');
    return true;
  }

  //       
  setOnTrackCallback(peerId, callback) {
    this.onTrackCallbacks.set(peerId, callback);
  }

  //     
  async updateStreamStatus() {
    console.log('MediasoupApi:    ');
    return true;
  }

  //       
  async request(type, data = {}) {
    return new Promise((resolve) => {
      try {
        socketApi.emit(type, data, (response) => {
          if (!response) {
            console.warn(`      ${type}`);
            resolve({});
            return;
          }
          resolve(response);
        });
      } catch (error) {
        console.error(`    ${type}:`, error);
        resolve({ error: error.message || '    ' });
      }
    });
  }

  //  
  cleanup() {
    console.log('MediasoupApi:  ');
    this.initialized = false;
    this.roomId = null;
    this.onTrackCallbacks.clear();
  }
}

const mediasoupApi = new MediasoupApi();
export default mediasoupApi; 