import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './HomePage.module.css';
import socketApi from '@/shared/api/socketApi';

export const HomePage = () => {
  const [name, setName] = useState('');
  const [roomCode, setRoomCode] = useState('');
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const navigate = useNavigate();

  //      
  useEffect(() => {
    //  
    const socket = socketApi.init();
    
    //  
    const checkConnection = () => {
      const connected = socketApi.isConnected();
      setIsConnected(connected);
      if (!connected) {
        console.log('  ...');
        setError('   . ,   .');
      } else {
        setError('');
      }
    };
    
    //   
    checkConnection();
    
    //    
    const connectionTimer = setInterval(checkConnection, 3000);

    //    
    socketApi.on('room-created', (data) => {
      setIsLoading(false);
      console.log(' :', data);
      
      if (data.success) {
        //        
        navigate(`/game?name=${encodeURIComponent(name)}&room=${encodeURIComponent(data.roomCode)}&host=true`);
      } else {
        //     
        setError(data.error || '   ');
      }
    });

    //     
    socketApi.on('room-joined', (data) => {
      setIsLoading(false);
      console.log('  :', data);
      
      if (data.success) {
        //       
        navigate(`/game?name=${encodeURIComponent(name)}&room=${encodeURIComponent(roomCode)}`);
      } else {
        //  
        setError(data.error || '    ');
      }
    });

    //   
    socketApi.on('connect_error', (err) => {
      console.error('  Socket.IO:', err);
      setError(`   : ${err.message}`);
      setIsLoading(false);
    });

    //   
    return () => {
      clearInterval(connectionTimer);
      socketApi.off('room-created');
      socketApi.off('room-joined');
      socketApi.off('connect_error');
    };
  }, [navigate, name, roomCode]);

  const handleCreateRoom = () => {
    if (!isConnected) {
      setError('   . ,    .');
      return;
    }

    if (!name.trim()) {
      setError('  ');
      return;
    }

    setError('');
    setIsLoading(true);
    
    try {
      console.log('    :', name);
      //     
      socketApi.createRoom(name);
    } catch (err) {
      console.error('   :', err);
      setError(`   : ${err.message}`);
      setIsLoading(false);
    }
  };

  const handleJoinRoom = () => {
    if (!isConnected) {
      setError('   . ,    .');
      return;
    }

    if (!name.trim()) {
      setError('  ');
      return;
    }

    if (!roomCode.trim()) {
      setError('  ');
      return;
    }

    setError('');
    setIsLoading(true);
    
    try {
      console.log('     :', roomCode, name);
      //      
      socketApi.joinRoom(roomCode, name);
    } catch (err) {
      console.error('    :', err);
      setError(`   : ${err.message}`);
      setIsLoading(false);
    }
  };

  return (
    <div className={styles.homePage}>
      <div className={styles.container}>
        <h1 className={styles.title}>Who Am I?</h1>
        <p className={styles.subtitle}>A fun game to play with your friends</p>
        
        {!isConnected && (
          <div className={styles.connectionStatus}>
            Connecting to server...
          </div>
        )}
        
        {error && <div className={styles.errorMessage}>{error}</div>}
        
        <div className={`${styles.formGroup} ${styles.nameInput}`}>
          <input
            type="text"
            className={styles.input}
            placeholder="Enter name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            disabled={isLoading || !isConnected}
          />
        </div>
        
        <div className={styles.formGroup}>
          <button 
            className={styles.createButton}
            onClick={handleCreateRoom}
            disabled={isLoading || !isConnected}
          >
            {isLoading ? 'Creating...' : 'Create room'}
          </button>
        </div>
        
        <div className={styles.divider}>or</div>
        
        <div className={styles.formGroup}>
          <input
            type="text"
            className={styles.input}
            placeholder="Room code"
            value={roomCode}
            onChange={(e) => setRoomCode(e.target.value.toUpperCase())}
            disabled={isLoading || !isConnected}
          />
        </div>
        
        <div className={styles.formGroup}>
          <button 
            className={styles.joinButton}
            onClick={handleJoinRoom}
            disabled={isLoading || !isConnected}
          >
            {isLoading ? 'Joining...' : 'Join'}
          </button>
        </div>
      </div>
    </div>
  );
};
